<template>
    <div class="alarm">
        <Topbar></Topbar>
        <mu-card v-for="item,index in alarmList">
          <!-- <mu-card-header :title="':'+item.groupName" :subTitle="':'+item.systemName+',:'+item.locationName+',:'+item.roomName">
          </mu-card-header> -->
          <mu-card-header :title="':'+item.groupName" :subTitle="':'+item.roomName">
          </mu-card-header>
          <!-- <mu-card-title title="Content Title" subTitle="Content Title"/> -->
          <div class="alarm-text">
          <span v-if="item.show == null || item.show == false">
            <b>:&nbsp;&nbsp;{{item.startTime | dateAll}}-{{item.endTime | dateAll}}</b><br/>
            <b>:</b>{{item.describes.substring(0,150)}}<a class="alarm-more" @click="showMore(item,index)">&nbsp;&nbsp;>>&nbsp;&nbsp;</a>
          </span>
          <span v-if="item.show== true">
            <b>:&nbsp;&nbsp;{{item.startTime | dateAll}}-{{item.endTime | dateAll}}</b><br/>
            <b>:</b>{{item.describes}}<a class="alarm-more" @click="showMore(item,index)">&nbsp;&nbsp;<<&nbsp;&nbsp;</a>
          </span>
          </div>
          <mu-card-actions>
            <mu-flat-button v-if="item.confirm == 1" icon="done" label="" primary/>
            <mu-flat-button v-if="item.confirm == 0" icon="search" label="" primary/>
          </mu-card-actions>
        </mu-card>
        <mu-raised-button label="" @click="loadMore" default fullWidth v-if="alarmList != null && args.totalPage != args.currentPage"/>
        <mu-raised-button label="" default fullWidth v-if="alarmList != null && args.totalPage == args.currentPage"/>
        <div class="bottom"></div>
        <Tabbar></Tabbar>
    </div>
</template>

<script>
import Tabbar from 'components/Tabbar';
import Topbar from 'components/Topbar';
import { MessageBox, Indicator } from 'mint-ui';
import * as type from './../../store/base/type';

export default{
  components: {
    Tabbar,
    Topbar,
  },
  data() {
    return {
      alarmList: null,
      args: {
        currentPage: 1,
        numberPerPage: 20,
        totalPage: null,
      },
    };
  },
  created() {
    this.getAlarmList();
  },
  methods: {
    back() {
      this.$router.go(-1);
    },
    getAlarmList() {
      Indicator.open();
      const self = this;
      this.$store.dispatch(type.ALARM_LIST, this.args).then((data) => {
        if (self.alarmList == null) {
          self.alarmList = data.data;
        } else {
          self.alarmList = self.alarmList.concat(data.data);
        }
        self.args.currentPage = data.currentPage;
        self.args.totalPage = data.totalPage;
        self.args.numberPerPage = data.numberPerPage;
        Indicator.close();
      }).catch((err) => {
        Indicator.close();
        MessageBox('', err);
        if (err === '') {
          self.$store.commit(type.USER_LOGOUT);
          self.$router.push({ name: 'Login' });
        }
      });
    },
    showMore(item, index) {
      item.show = !item.show;
      this.$set(this.alarmList, index, item);
      console.log(item.show);
    },
    loadMore() {
      this.args.currentPage = this.args.currentPage + 1;
      this.getAlarmList();
    },
  },
};
</script>
<style lang="css" scoped>
    .alarm{
        text-align: left;
        margin-top: 60px;
        width: 100%;
    }
    .alarm-text {
      margin: 0px 15px 0px 15px;
    }
    .alarm-text span{
      display: inline-block;
      /* text-decoration: none; */
      /* word-wrap: break-word; */
      word-break: break-all;
    }
    .alarm-more {
      color: black;
      font-weight: bold;
    }
    .bottom {
      height: 56px;
    }
</style>



